import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: resolve=>require(["@/components/login.vue"], resolve),
      meta: {
        isLogin: false // 是否需要判断是否登录,这里是不需要判断
      },
    },
    {
      path: '/',
      name: 'HelloWorld',
      redirect:'/login',
      component: resolve=>require(["@/components/HelloWorld.vue"], resolve),
      meta: {
        isLogin: true  // 是否需要判断是否登录,这里是需要判断
      },
      children:[
        {
          path: '/hxsj',
          name: 'hxsj',
		      component: resolve=>require(["@/components/hexinshuju.vue"], resolve),
        },
        {
          path: '/nlpg',
          name: 'nlpg',
		      component: resolve=>require(["@/components/nenglipinggu.vue"], resolve),
        },
        {
          path: '/zgd',
          name: 'zgd',
		      component: resolve=>require(["@/components/zhengaidian.vue"], resolve),
        },
        {
          path: '/detail',
          name: 'detail',
          component: resolve=>require(["@/components/detail.vue"], resolve),
        },
        {
          path: '/schoolMap',
          name: 'schoolMap',
          component: resolve=>require(["@/components/schoolMap.vue"], resolve),
        },
        {
          name: 'students',
		      path: '/students',
		      component: resolve=>require(["@/components/students.vue"], resolve),
        },
        {
		      path: '/school',
		      component: resolve=>require(["@/components/school.vue"], resolve),
        },
        {
		      path: '/bxjf',
		      component: resolve=>require(["@/components/banxuejingfei.vue"], resolve),
        },
        {
		      path: '/szdw',
		      component: resolve=>require(["@/components/shiziduiwu.vue"], resolve),
        },
        {
		      path: '/xsgz',
		      component: resolve=>require(["@/components/xueshenggongzuo.vue"], resolve),
        },
        {
		      path: '/djgz',
		      component: resolve=>require(["@/components/dangjiangongzuo.vue"], resolve),
        },
        {
		      path: '/xsqk',
		      component: resolve=>require(["@/components/xueshengqingkuang.vue"], resolve),
        },
        {
		      path: '/zyts',
		      component: resolve=>require(["@/components/zhuanyetese.vue"], resolve),
        },
        {
		      path: '/kcgk',
		      component: resolve=>require(["@/components/kechenggaikuang.vue"], resolve),
        },
        {
		      path: '/jysx',
		      component: resolve=>require(["@/components/jiaoxueshixun.vue"], resolve),
        },
        {
		      path: '/zxxx',
		      component: resolve=>require(["@/components/zaixiaoxuexi.vue"], resolve),
        },
        {
		      path: '/szjg',
		      component: resolve=>require(["@/components/shizijiegou.vue"], resolve),
        },
        {
		      path: '/jyqk',
		      component: resolve=>require(["@/components/jiuyeqingkuang.vue"], resolve),
        },
        {
		      path: '/jxsx',
		      component: resolve=>require(["@/components/jiaoxueshixun.vue"], resolve),
        },
        {
		      path: '/ryqk',
		      component: resolve=>require(["@/components/renyuanqingkuang.vue"], resolve),
        },
        {
		      path: '/zyjn',
		      component: resolve=>require(["@/components/zhuanyejineng.vue"], resolve),
        },
        {
		      path: '/zxkt',
		      component: resolve=>require(["@/components/zaixianketang.vue"], resolve),
        },
        {
		      path: '/xxkc',
		      component: resolve=>require(["@/components/xianxiakecheng.vue"], resolve),
        },
        {
		      path: '/zwzg',
		      component: resolve=>require(["@/components/ziwozhengai.vue"], resolve),
        },
        {
		      path: '/test',
		      component: resolve=>require(["@/components/test.vue"], resolve),
        },
      ]
    },
  ]
})



